"use client";

import React, { useState, useContext } from "react";
import { useRouter } from "next/router";
import Head from "next/head";
import Link from "next/link";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import CheckoutForm from "../components/CheckoutForm";
import StoreContext from "../context/StoreContext";
import { CheckCircle } from "lucide-react";

export default function CheckoutPage() {
  const { state } = useContext(StoreContext);
  const { user, cart } = state;
  const router = useRouter();
  const [orderComplete, setOrderComplete] = useState(false);
  const [orderData, setOrderData] = useState(null);

  // Redirect to login if not authenticated
  if (!user) {
    router.push('/login?returnUrl=/checkout');
    return null;
  }

  // Redirect to cart if cart is empty
  if (cart.length === 0 && !orderComplete) {
    router.push('/cart');
    return null;
  }

  const handleOrderComplete = (order) => {
    setOrderData(order);
    setOrderComplete(true);
  };

  if (orderComplete && orderData) {
    return (
      <>
        <Head>
          <title>Order Confirmation - Lewkin</title>
          <meta name="description" content="Your order has been successfully placed." />
        </Head>

        <div className="min-h-screen bg-white dark:bg-black">
          <Navbar />
          
          <div className="max-w-2xl mx-auto px-4 sm:px-6 lg:px-8 py-16">
            <div className="text-center">
              <CheckCircle className="mx-auto h-16 w-16 text-green-600 mb-4" />
              <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-4">
                Order Confirmed!
              </h1>
              <p className="text-gray-600 dark:text-gray-400 mb-8">
                Thank you for your purchase. Your order has been successfully placed.
              </p>
              
              <div className="bg-gray-50 dark:bg-gray-800 rounded-lg p-6 mb-8">
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 text-sm">
                  <div>
                    <p className="text-gray-600 dark:text-gray-400">Order ID</p>
                    <p className="font-semibold text-gray-900 dark:text-white">{orderData.orderId}</p>
                  </div>
                  <div>
                    <p className="text-gray-600 dark:text-gray-400">Total Amount</p>
                    <p className="font-semibold text-gray-900 dark:text-white">${orderData.total.toFixed(2)}</p>
                  </div>
                  <div>
                    <p className="text-gray-600 dark:text-gray-400">Payment Method</p>
                    <p className="font-semibold text-gray-900 dark:text-white capitalize">
                      {orderData.paymentMethod.replace('_', ' ')}
                    </p>
                  </div>
                  <div>
                    <p className="text-gray-600 dark:text-gray-400">Status</p>
                    <p className="font-semibold text-green-600">Paid</p>
                  </div>
                </div>
              </div>

              <div className="space-y-4">
                <p className="text-gray-600 dark:text-gray-400">
                  We'll send you a confirmation email with tracking information once your order ships.
                </p>
                
                <div className="flex flex-col sm:flex-row gap-4 justify-center">
                  <Link
                    href="/products"
                    className="bg-black dark:bg-white text-white dark:text-black px-6 py-3 rounded-lg font-semibold hover:bg-gray-800 dark:hover:bg-gray-200 transition-colors"
                  >
                    Continue Shopping
                  </Link>
                  <Link
                    href="/account"
                    className="border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 px-6 py-3 rounded-lg font-semibold hover:bg-gray-50 dark:hover:bg-gray-800 transition-colors"
                  >
                    View Orders
                  </Link>
                </div>
              </div>
            </div>
          </div>

          <Footer />
        </div>
      </>
    );
  }

  return (
    <>
      <Head>
        <title>Checkout - Lewkin</title>
        <meta name="description" content="Complete your purchase securely with our checkout process." />
        <meta name="viewport" content="width=device-width, initial-scale=1" />
      </Head>

      <div className="min-h-screen bg-white dark:bg-black">
        <Navbar />
        
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          {/* Breadcrumb */}
          <div className="flex items-center space-x-2 text-sm text-gray-600 dark:text-gray-400 mb-8">
            <Link href="/cart" className="hover:text-gray-900 dark:hover:text-white">
              Cart
            </Link>
            <span>/</span>
            <span className="text-gray-900 dark:text-white">Checkout</span>
          </div>

          <div className="mb-8">
            <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-4">
              Checkout
            </h1>
            <p className="text-gray-600 dark:text-gray-400">
              Complete your order by providing your shipping details and payment information
            </p>
          </div>

          <CheckoutForm onOrderComplete={handleOrderComplete} />
        </div>

        <Footer />
      </div>
    </>
  );
}
