"use client";

import React, { useContext, useState } from "react";
import Link from "next/link";
import StoreContext from "../context/StoreContext";
import { Menu, X, ShoppingCart, Heart } from "lucide-react";

export default function Navbar() {
  const { state } = useContext(StoreContext);
  const { user, cart, wishlist } = state;
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <nav className="bg-white dark:bg-black border-b border-gray-200 dark:border-gray-800">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 flex justify-between items-center h-16">
        <div className="flex items-center">
          <Link href="/" className="text-2xl font-bold text-gray-900 dark:text-white">
            Lewkin
          </Link>
        </div>
        <div className="hidden md:flex space-x-6 items-center">
          <Link href="/" className="text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
            Home
          </Link>
          <Link href="/products" className="text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
            Products
          </Link>
          {user && (
            <>
              <Link href="/wishlist" className="relative text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                <Heart className="inline-block mr-1" size={18} />
                Wishlist
                {wishlist.length > 0 && (
                  <span className="absolute -top-2 -right-3 bg-red-600 text-white rounded-full text-xs w-5 h-5 flex items-center justify-center">
                    {wishlist.length}
                  </span>
                )}
              </Link>
              <Link href="/cart" className="relative text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                <ShoppingCart className="inline-block mr-1" size={18} />
                Cart
                {cart.length > 0 && (
                  <span className="absolute -top-2 -right-3 bg-red-600 text-white rounded-full text-xs w-5 h-5 flex items-center justify-center">
                    {cart.length}
                  </span>
                )}
              </Link>
              <Link href="/account" className="text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                Account
              </Link>
              {user.isAdmin && (
                <Link href="/admin-dashboard" className="text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                  Admin
                </Link>
              )}
            </>
          )}
          {!user && (
            <>
              <Link href="/login" className="text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                Login
              </Link>
              <Link href="/register" className="text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                Register
              </Link>
            </>
          )}
        </div>
        <div className="md:hidden">
          <button
            onClick={() => setMenuOpen(!menuOpen)}
            aria-label="Toggle menu"
            className="text-gray-700 dark:text-gray-300 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500"
          >
            {menuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </div>
      {menuOpen && (
        <div className="md:hidden bg-white dark:bg-black border-t border-gray-200 dark:border-gray-800 px-4 py-3 space-y-2">
          <Link href="/" className="block text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
            Home
          </Link>
          <Link href="/products" className="block text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
            Products
          </Link>
          {user && (
            <>
              <Link href="/wishlist" className="block text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                Wishlist ({wishlist.length})
              </Link>
              <Link href="/cart" className="block text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                Cart ({cart.length})
              </Link>
              <Link href="/account" className="block text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                Account
              </Link>
              {user.isAdmin && (
                <Link href="/admin-dashboard" className="block text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                  Admin
                </Link>
              )}
            </>
          )}
          {!user && (
            <>
              <Link href="/login" className="block text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                Login
              </Link>
              <Link href="/register" className="block text-gray-700 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white">
                Register
              </Link>
            </>
          )}
        </div>
      )}
    </nav>
  );
}
